import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			bannerInfo: {
			  'imgUrl': '/static/category/c1.png',
			  'name': '大家都在买的'
			},
			categoryFilter: false,
			filterCategory: [],
			goodsList: [],
			type: 'hot',
			categoryId: 0,
			currentSortType: 'default',
			currentSort: 'add_time',
			currentSortOrder: 'desc',
			page: 1,
			limit: 10
		}
	},
	async onLoad(options){
		this.type = options.type
		this.loadData();
	},
	onPullDownRefresh() {
		this.productdata(true);
	},
	methods: {
		loadData(flag) {
			uni.showLoading({title: '正在加载'})
			let url = '/goods/list?isHot=true&page=1&limit=100&order=desc&sort=add_time&categoryId=0'
			if(this.type !== 'hot') {
				url = '/goods/list?isNew=true&page=1&limit=100&order=desc&sort=add_time&categoryId=0'
				uni.setNavigationBarTitle({ title:'新品首发' })
			} else {
				uni.setNavigationBarTitle({ title:'人气推荐' })
			}
			api.httpget(url).then((res) => {
				let respons = res.data;
				uni.hideLoading()
				if(respons.errno == 0) {
					this.goodsList = respons.data.list
				}
			})
		}
	}
}
